import { nextTick, ref } from 'vue';
export default {
  name: 'BaseMenuNav',

  setup() {
    const openMenuNav = ref(false);
    const currentCollapse = ref(-1);

    const handleOpen = (key, keyPath) => {
      currentCollapse.value = -1;
      nextTick(() => {
        currentCollapse.value = +key;
      }); // console.log(key, keyPath)
    };

    const handleClose = (key, keyPath) => {
      currentCollapse.value = -1;
      nextTick(() => {
        currentCollapse.value = +key;
      }); // console.log(key, keyPath)
    };

    return {
      openMenuNav,
      handleOpen,
      handleClose,
      currentCollapse
    };
  }

};